    }

    @media (min-width:768px) {
        .shapedividers_com-9543::before {
            background-size: 100% 115px;
            background-position: 50% 0%;
        }
    }

    @media (min-width:1025px) {
        .shapedividers_com-9543::before {
            bottom: -0.1vw;
