/**
	Displays the individual timers so that you can change their values
*/
.timer-gallery {
    background-color: transparent;
    scrollbar-color: var(--clr-secondary-blue);
    scrollbar-width: thin;
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    max-height: 100vh;
    width: 100%;
    overflow-y: visible;

    // overflow-x: auto;
    // overscroll-behavior-inline: contain;
    // scroll-snap-type: inline mandatory;
    flex: 1;

    *,
    *:before,
    *:after {
        box-sizing: border-box;
    }



    .timer-wrapper {
        scrollbar-color: var(--clr-secondary-blue);
        scrollbar-width: thin;

        display: flex;
        justify-content: stretch;
        max-height: 100vh;
        width: 100%;
        overflow-x: auto !important;
        overflow-y: visible !important;
        overscroll-behavior-inline: contain;
        scroll-snap-type: inline mandatory;
        flex: 1;
        position: relative;

    }
}

.gallery__btn {
    position: fixed;
}

.timer__decrease-time,
.timer__increase-time {
    position: absolute;
    display: flex;
    flex-direction: column;

}

.timer__increase-time {
    top: -8.25rem;
    // bottom: -8.25rem;
    // left: 90%;
    isolation: isolate;
}

.timer__decrease-time {
    // display: none;
    bottom: -8.45rem;
}

.time-value {
    position: relative;
}

.preview-timer-wrapper {
    // padding-top: 10%;
    overflow: visible;
    position: relative;

}

.timer-holder {

    display: flex;
    justify-content: stretch;
    max-height: 100vh;
    width: 100%;
}

.preview-timer {
    scroll-snap-align: start;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-y: visible;
    height: 100%;
    z-index: 2;


    &__number {
        position: absolute;
        top: 10%;
        left: 50%;
        transform: translateX(-50%);
    }

    .value-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 2rem;
    }

    position: relative;
    width: 100vw;
    min-width: 100%;

    .speed-dial {
        position: absolute;
        top: 2rem;
        left: 2rem;

        .speed-dial__actions {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;
            margin-top: 0.5rem;

            &.bottom {
                margin-bottom: 0.5rem;
                margin-top: 0;
            }
        }
    }
}

.time-value {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    +.time-value {
        // margin-top: 8rem;
    }

    // .value {
    // 	font-size: var(--text-xxxl);
    // 	font-weight: var(--fw-thin);
    // }


    button {
        display: inline-flex;
        // background-color: rgba(255, 255, 255, 0.503);
        font-weight: var(--fw-light);
        transform: scale(1);
        transition: background-color 100ms ease-out, border 100ms ease-out, transform 100ms ease-out;
        // border-radius: 0 !important;
        // padding: 1.0rem 2rem !important;

        // font-size: var(--text-sm);

        &:hover {
            border: 1px solid currentColor;
            background-color: var(--clr-primary-orange);
            transform: scale(1.2)
        }
    }
}

.timer__separator {
    font-size: var(--text-xxxl);
    font-weight: var(--fw-thin);
    text-align: center;
    vertical-align: middle;
    display: flex;
    align-items: center;
    justify-content: center;
}
